.favorites-container {
    display: flex;
    margin: auto;
    flex-wrap: wrap;
    justify-content: center;
    gap: 2.5rem;
    padding: 2rem 0;
}

.no-item-style {
    text-align: center;
    font-size: 1.25rem;
    color: black;
    font-weight: 800;
}

@media (max-width:768px){
    .favorites-container{
        max-width: 640px;
    }
}